/**
 * ReferralContainer.js
 * Get the sharecode from user details if logged
 * in and render in the referral page
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Referral from '../../components/Referral/Referral';

class ReferralContainer extends Component {
  render() {
    const { userDetails, login } = this.props;
    const shareCode = userDetails ? userDetails.share_code : null;
    return (
      <Referral
        login={login}
        shareCode={shareCode}
      />
    );
  }
}

const mapStateToProps = state => ({
  userDetails: state.userDetails.data,
  login: state.login,
});

ReferralContainer.defaultProps = {
  userDetails: null,
};

ReferralContainer.propTypes = {
  userDetails: PropTypes.object,
  login: PropTypes.object.isRequired,
};

export default connect(mapStateToProps)(ReferralContainer);
