import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import {
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Collapse,
  CardBody,
  Card,
} from 'reactstrap';

// import arrow
import Arrow from 'assets/images/forms/ArrowGradient.svg';

import './TnC.scss';

class TnC extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleCollapse = this.toggleCollapse.bind(this);

    this.state = {
      activeTab: '1',
      collapse: true,
    };
  }
  componentDidMount() {
    window.scroll(0, 0);
  }
  toggleCollapse() {
    this.setState({ collapse: !this.state.collapse });
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }
  render() {
    const { data } = this.props;
    const rotateArrow = this.state.collapse ? 'turn' : 'arrow';
    const activeStyle = this.state.activeTab === '1' ? 'active' : '';

    return (
      <Fragment>
        <Nav
          tabs
          className="tos-container"
          onClick={() => { this.toggleCollapse(); }}
        >
          <NavItem style={{ flex: 1 }}>
            <NavLink
              className={activeStyle}
              onClick={() => { this.toggle('1'); }}
            >
              Terms &amp; Conditions
            </NavLink>
          </NavItem>
          <img src={Arrow} className={rotateArrow} alt="arrow" />
        </Nav>
        <TabContent activeTab={this.state.activeTab} className="tos-inner">
          <TabPane tabId="1">
            <Collapse isOpen={this.state.collapse}>
              <Card>
                <CardBody>
                  <ol>
                    {
                      _.map(data.data.contents, (list, index) => (
                        <li key={index}>{list}</li>
                      ))
                    }
                  </ol>
                </CardBody>
              </Card>
            </Collapse>
          </TabPane>
        </TabContent>
      </Fragment>
    );
  }
}

TnC.defaultProps = {

};
TnC.propTypes = {
  data: PropTypes.object.isRequired,
};

export default TnC;
